@import "main";

img {
    max-width: 100%;
    height: auto;
}

.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30%;
  }

.imageBox {
    position: relative;
    top: 0;
}

.imageBox .hoverImg {
    position: absolute;
    top: 0;
    display: none;
}

.imageBox:hover .hoverImg {
    display: block;
}

.background {
    position: relative;
}

.overlay {
    position: absolute;
    top: 15%;
    left: 0;
    right: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 20%;
    box-shadow: purple 8px 5px 10px 10px; 
}

.overlay_center {
    position: absolute;
    top: 50px;
    left: 0;
    right: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 20%;
    box-shadow: purple 8px 5px 10px 10px; 
}

.img_text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(201, 185, 201, 0.8);
    color: rgb(77, 71, 77);
    visibility: hidden;
    opacity: 0;
    text-align: center;
}

.overlay:hover .img_text {
   visibility: visible;
   opacity: 1;
}